.popup__bg {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 98;
  background: rgba(0, 0, 0, 0.5);
  display: none;
}

.wrap__popup {
  width: 470px;
  height: 390px;
  color: #000;
  margin-left: auto;
  margin-right: auto;
  margin-top: 50px;
}

@media only screen and (max-width: 1330px) {
  .wrap__popup {
    margin-top: 45px;
    width: 400px;
  }
}

@media only screen and (max-width: 1330px) and (max-width: 768px) {
  .wrap__popup {
    margin-top: 30px;
    width: 350px;
    height: 300px;
  }
}

@media only screen and (max-width: 1330px) and (max-width: 768px) and (max-width: 560px) {
  .wrap__popup {
    margin-top: 20px;
    width: 100%;
    height: 270px;
  }
}

@media only screen and (max-width: 1330px) and (max-width: 768px) and (max-width: 560px) and (max-width: 420px) {
  .wrap__popup {
    height: 260px;
  }
}

.wrap__popup h2 {
  font-family: 'montbold', Arial, Helvetica, sans-serif;
  font-size: 36px;
  line-height: 44px;
  color: #000;
  text-align: center;
  padding-bottom: 38px;
}

@media only screen and (max-width: 1330px) {
  .wrap__popup h2 {
    font-size: 32px;
    line-height: 38px;
  }
}

@media only screen and (max-width: 1330px) and (max-width: 768px) {
  .wrap__popup h2 {
    font-size: 26px;
    line-height: 30px;
    padding-bottom: 30px;
  }
}

@media only screen and (max-width: 1330px) and (max-width: 768px) and (max-width: 560px) {
  .wrap__popup h2 {
    font-size: 18px;
    line-height: 20px;
    padding-bottom: 20px;
  }
}

@media only screen and (max-width: 1330px) and (max-width: 768px) and (max-width: 560px) and (max-width: 420px) {
  .wrap__popup h2 {
    font-size: 16px;
    line-height: 18px;
    padding-bottom: 15px;
  }
}

.wrap__popup input {
  border: 1px solid #979696;
  box-sizing: border-box;
  display: block;
  width: 90%;
  height: 48px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 20px;
  font-family: 'mont', Arial, Helvetica, sans-serif;
  font-size: 18px;
  line-height: 22px;
  color: #000;
}

@media only screen and (max-width: 1330px) {
  .wrap__popup input {
    font-size: 15px;
  }
}

@media only screen and (max-width: 1330px) and (max-width: 768px) {
  .wrap__popup input {
    height: 38px;
    padding: 0px 15px;
    font-size: 12px !important;
    line-height: 16px;
  }
}

@media only screen and (max-width: 1330px) and (max-width: 768px) and (max-width: 560px) {
  .wrap__popup input {
    width: 80%;
    height: 32px;
    padding: 0px 10px;
    font-size: 10px !important;
  }
}

@media only screen and (max-width: 1330px) and (max-width: 768px) and (max-width: 560px) and (max-width: 420px) {
  .wrap__popup input {
    height: 29px;
    font-size: 9px !important;
  }
}

.wrap__popup label {
  font-family: 'mont';
  font-size: 12px;
  line-height: 15px;
  color: #979696;
  cursor: pointer;
}

@media only screen and (max-width: 1330px) {
  .wrap__popup label {
    font-size: 10px;
  }
}

@media only screen and (max-width: 1330px) and (max-width: 560px) {
  .wrap__popup label {
    font-size: 8px;
    padding-left: 10px;
  }
}

@media only screen and (max-width: 1330px) and (max-width: 560px) and (max-width: 420px) {
  .wrap__popup label {
    font-size: 7px;
    padding-left: 5px;
  }
}

.wrap__popup form div {
  display: flex;
  align-items: center;
}

.wrap__popup input[type='tel'] {
  margin-top: 15px;
  margin-bottom: 30px;
}

@media only screen and (max-width: 420px) {
  .wrap__popup input[type='tel'] {
    margin-bottom: 20px;
  }
}

.wrap__popup input[type='checkbox'] {
  display: inline;
  width: 25px;
  height: 23px;
  margin-left: 5%;
  margin-right: 25px;
  padding: 0;
  cursor: pointer;
}

@media only screen and (max-width: 768px) {
  .wrap__popup input[type='checkbox'] {
    width: 20px;
    height: 18px;
    margin-left: 5%;
    margin-right: 10px;
  }
}

@media only screen and (max-width: 768px) and (max-width: 560px) {
  .wrap__popup input[type='checkbox'] {
    width: 15px;
    margin-left: 10%;
    margin-right: 0;
  }
}

@media only screen and (max-width: 768px) and (max-width: 560px) and (max-width: 420px) {
  .wrap__popup input[type='checkbox'] {
    width: 14px;
  }
}

.wrap__popup input[type='button'] {
  margin-top: 30px;
  background-color: #FFA800;
  text-shadow: 0px 0px 6px rgba(0, 0, 0, 0.5);
  color: #fff;
  box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
  -webkit-appearance: none;
  border-radius: 0;
  border: 0;
  cursor: pointer;
}

@media only screen and (max-width: 560px) {
  .wrap__popup input[type='button'] {
    margin-top: 20px;
  }
}

.wrap__popup input[type='button']:hover {
  background-color: #FFC34E;
}

.wrap__popup input[placeholder] {
  font-family: 'mont';
  font-size: 18px;
  line-height: 22px;
}
