.block2 {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 1250px;
    @include r(1065) {
        height: 850px;
        @include r(680) {
            height: 750px;
            @include r(480) {
                height: 730px; } } }

    .wrap__block2 {
        max-width: 1620px;
        height: 1113px;
        background-image: url(../img/BC-block2.jpg);
        background-position: center;
        background-size: contain;
        background-repeat: no-repeat;
        @include r(1500) {
            height: 900px;
            @include r(1330) {
                height: 930px;
                @include r(1065) {
                    padding-top: 100px;
                    height: 100%;
                    background-size: cover;
                    @include r(680) {
                        padding-top: 45px;
                        background-size: contain;
                        @include r(480) {
                            padding: 0; } } } } }
        .line1 {
            display: flex;
            height: 500px;
            margin-bottom: 30px;
            @include r(1500) {
                height: 450px;
                @include r(1065) {
                    height: 320px;
                    @include r(680) {
                        flex-direction: column-reverse; } } }
            .aboutCompany {
                max-width: 550px;
                margin-left: 68px;
                display: flex;
                flex-direction: column;
                justify-content: center;
                @include r(1065) {
                    max-width: 300px;
                    margin-left: 50px;
                    @include r(680) {
                        align-items: center;
                        width: 100%;
                        max-width: 100%;
                        margin: 0; } }
                h2 {
                    display: block;
                    font-family: 'montbold', Arial, Helvetica, sans-serif;
                    font-size: 44px;
                    line-height: 66px;
                    letter-spacing: 0.2px;
                    color: #272727;
                    padding-bottom: 25px;
                    @include r(1500) {
                        font-size: 42px;
                        @include r(1065) {
                            font-size: 32px;
                            padding-bottom: 15px;
                            @include r(680) {
                                font-size: 22px;
                                line-height: 66px;
                                padding-bottom: 10px;
                                @include r(480) {
                                    font-size: 18px;
                                    padding: 0; } } } } } }
            img {
                padding-left: 100px;
                @include r(1330) {
                    padding: 0;
                    width: 50%;
                    @include r(1065) {
                        width: 45%;
                        height: 100%;
                        object-fit: cover;
                        @include r(975) {
                            width: 50%;
                            @include r(680) {
                                width: 45%;
                                @include r(480) {
                                    height: 44%;
                                    width: 47%;
                                    @include r(420) {
                                        width: 55%;
                                        @include r(360) {
                                            width: 65%; } } } } } } } } } } }
p {
    font-family: 'mont', Arial, Helvetica, sans-serif;
    font-weight: 100;
    font-size: 32px;
    line-height: 42px;
    color: #272727;
    @include r(1500) {
        font-size: 28px;
        line-height: 40px;
        @include r(1065) {
            font-size: 20px;
            line-height: 28px;
            @include r(680) {
                font-size: 16px;
                line-height: 20px;
                margin-bottom: 20px;
                width: 300px;
                @include r(480) {
                    font-size: 14px;
                    line-height: 16px;
                    max-width: 240px; } } } } }

b {
    font-family: 'montbold'; }
.line2 {
    display: flex;
    justify-content: space-around;
    height: 570px;
    @include r(1500) {
        height: 520px;
        @include r(1330) {
            height: 50%;
            @include r(1065) {
                margin-top: 30px;
                height: 350px;
                @include r(680) {
                    flex-direction: column;
                    align-items: flex-end; } } } }
    .endAbout {
        max-width: 705px;
        margin-right: 80px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding-left: 90px;
        @include r(1330) {
            padding: 0;
            margin-right: 50px;
            @include r(680) {
                align-items: center;
                margin: 0;
                @include r(480) {
                    width: 100%; } } }
        @include r(1600) {
            p {
                font-size: 24px;
                line-height: 36px;
                @include r(1065) {
                    font-size: 20px;
                    line-height: 24px;
                    @include r(680) {
                        font-size: 16px;
                        line-height: 20px;
                        width: 80%;
                        @include r(480) {
                            font-size: 14px;
                            line-height: 18px;
                            max-width: 280px; } } } } } }

    img {
        padding-bottom: 50px;
        padding-right: 60px;
        object-fit: cover;
        @include r(1330) {
            padding-right: 0;
            width: 50%;
            height: 100%;
            @include r(680) {
                width: 45%;
                padding: 0;
                @include r(480) {
                    height: 40%;
                    width: 48%;
                    @include r(420) {
                        width: 55%;
                        @include r(360) {
                            width: 65%; } } } } } } }
@include r(680) {
    .endAbout {
        span {
            display: block;
            padding-top: 15px; } } }
